    "Hiragino Sans GB W3", "Microsoft YaHei UI", "Microsoft YaHei",
    "WenQuanYi Micro Hei", sans-serif;
  font-size: 14px;
  color: #444;
}
/* 以上为样式初始化 */
.app-head {
  width: 100%;
  font-size: 14px;
  color: #fff;
  background: #ff9b6a;
  text-align: center;
  padding: 9px 0;
}
.app-footer{
 background:#999; 
 text-align: center;
 height:30px;
 line-height: 30px;
 color:#fff;
}
.det-qut {
  padding: 0 15px;
  border-bottom: 1px solid #dbdbdb;
}
.det-qut .info {
  color: #999;
  font-size: 1.4rem;
  line-height: 20px;
  overflow: hidden;
}
/* 头像尺寸 */
.det-qut .author img {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  vertical-align: middle;
  margin: 0 6px 2px 0;
}
.det-qut .det-nub {
  font-size: 0.9rem;
  color: #666;
  line-height: 30px;
}
/* 清除浮动 */
.clearfix:after {
  clear: both;
  height: 0;
  overflow: hidden;
  display: block;
  visibility: hidden;
  content: ".";
}
.det-qut .author .date {
  margin-left: 10px;
}
/* 回答者样式 */
.a-list li {
  padding: 15px;
  border-bottom: 1px solid #dbdbdb;
}
.a-list .user {
  padding-top: 10px;
}
.a-list .user,
.a-detail .user {
  width: 38px;
  float: left;
  vertical-align: top;
}
.a-list .user img {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-left: 3px;
}
.a-list .user-bar {
  padding-top: 10px;
}
.a-list .user-bar,
.a-detail .user-bar {
  position: relative;
  line-height: 24px;
}
.a-list .detail_answer {
  right: -18px;
  top: -15px;
}
.a-list .answer-medal {
  top: -12px;
  right: 0;
}
.detail_answer {
  right: -3px;
  top: -15px;
}
.answer-medal {
  position: absolute;
  right: 15px;
  top: -8px;
}
.a-list .user-bar a,
.a-detail .user-bar a {
  font-size: 0.9rem;
  color: #999;
}
.a-list .user-bar .lv,
.a-detail .user-bar .lv {
  color: #ff9d00;
  margin-left: 3px;
}
/* 正文样式 */
.a-list .a-desc {
  line-height: 2.2rem;
}
.a-list .a-desc,
.a-detail .a-desc {
  line-height: 2rem;
}
.a-list .a-desc {
  display: block;
  /* font-size: 1.4rem; */
  color: #111;
  line-height: 1.6em;
  padding: 4px 0 0 36px;
}
/* 阅读数与评论样式 */
.a-list .a-btns {
  clear: both;
  padding: 15px 0 0 36px;
}
.a-list .a-btns a {
  display: inline-block;
  line-height: 28px;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
}
.a-list .a-btns .btn1 {
  border: 0 none;
  padding: 0;
  font-size: 1.35rem;
  color: #fc9d27;
  font-weight: bold;
}
.a-list .a-btns .btn1 {
  padding: 0 17px;
}
.a-list .a-btns .btn1 i {
  float: left;
  margin-top: -3px;
}
.a-list .a-btns .btn1 i {
  width: 25px;
  height: 25px;
  display: inline-block;
  background: url(data:image/png;base64,) no-repeat;
  background-size: cover;
  vertical-align: middle;
  margin: 0 5px 1px 0;
}
.a-list .a-btns .btn1 span,
.a-list .a-btns .btn1 strong {
  float: left;
}
.a-list .a-btns .btn1 span,
.a-list .a-btns .btn1 strong {
  float: left;
}
.a-list .a-btns .btn1 span {
  font-size: 1.35rem;
  color: #999;
}
/* 最佳回答 */
.zl_wrapper {
  margin-left: 36px;
}
.a-list .a-desc span.tz_adop,
.a-desc span.tz_adop {
  font-size: 0.95rem;
  color: #999;
  margin-right: 3px;
}
.a-list .user-bar .i-guide, .a-detail .user-bar .i-guide {
    display: inline-block;
    line-height: 1em;
    height: 9px;
    font-size: 0.5rem;
    color: #fff;
    background-color: #70c2b9;
    padding: 3px 6px;
    border-radius: 8px;
}
.a-list .user-bar .i-guide {
    margin-bottom: 2px;
}


